.container {
  padding: 16px 24px;
  flex-direction: column;
  gap: 8px;
  align-self: stretch;

  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 56px;
  text-decoration: none;
  color: #0e0e0e;
  background-color: #f1b550;
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  letter-spacing: -0.3px;
}
