.orderContainer {
  background-color: transparent;
  overflow: visible;
  transform: translateY(-100px);
  .order {
    transition: all 0.2s ease-in-out;
    min-height: 300px;
    height: 100%;
    background-color: #ffffff;

    display: flex;
    padding: 16px;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    align-self: stretch;
    a {
      background: #f3f4f4;
      width: inherit;
      flex-shrink: 0;
    }

    .pipetsTakTupoRabotaetPochemuuu {
      height: 100%;
      display: flex;
      flex-direction: column;
      gap: 15px;
    }
    p {
      color: var(--neutral-900, #0e0e0e);
      font-size: 16px;
      font-family: Inter;
      font-weight: 600;
      line-height: 28px;
      letter-spacing: -0.4px;
      margin: 0;
    }

    h2 {
      color: var(--neutral-900, #0e0e0e);
      font-size: 20px;
      font-family: Inter;
      font-weight: 600;
      line-height: 28px;
      letter-spacing: -0.4px;
      margin: auto;
    }
    .rect {
      width: 48px;
      height: 4px;
      background: #f3f4f4;
    }

    .polosa {
      width: 328px;
      height: 8px;
      flex-shrink: 0;
      background: linear-gradient(90deg, #ffffff, #ffffff, #0065ff, #ffffff, #ffffff);
      background-size: 150%;
      -webkit-animation: Animation 3s ease-in-out infinite;
      -moz-animation: Animation 3s ease-in-out infinite;
      animation: Animation 3s ease-in-out infinite;
    }

    @-webkit-keyframes Animation {
      0% {
        background-position: 5% 0%;
      }
      50% {
        background-position: 91% 100%;
      }
      100% {
        background-position: 10% 0%;
      }
    }
    @-moz-keyframes Animation {
      0% {
        background-position: 10% 0%;
      }
      50% {
        background-position: 91% 100%;
      }
      100% {
        background-position: 10% 0%;
      }
    }
    @keyframes Animation {
      0% {
        background-position: 10% 0%;
      }
      50% {
        background-position: 91% 100%;
      }
      100% {
        background-position: 10% 0%;
      }
    }
  }
}
