.container {
  position: absolute;
  top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  width: 100%;
}

.topBar {
  display: flex;
  height: 64px;
  padding: 4px 12px 4px 8px;
  gap: 8px;
  justify-content: flex-end;
  align-items: center;
  gap: var(--spacing-100, 8px);
  border-radius: 1000px;
  background: var(--neutral-900, #0e0e0e);
  margin: auto;

  p {
    color: var(--neutral-0, #fff);
    /* body-XL-700 */
    font-size: 20px;
    font-family: Inter;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: -0.4px;
    margin: 0;
  }

  img {
    width: 48px;
    height: 48px;
    border-radius: 48px;
    border: 2px solid var(--neutral-0, #fff);
  }
}
