.container {
  display: flex;
  flex-direction: column;
  justify-content: left;
  gap: 10px;

  padding: 0 50px;

  p {
    display: inline;
    width: fit-content;
    margin: 0;
    padding-left: 15px;
    font-size: 20px;
    font-weight: 400;
    color: #888888;
  }

  input {
    background: transparent;
    font-size: 25px;
    font-weight: 400;
    border: none;
    border-bottom: 1px solid black;
    width: 100%;
  }
}
